.findChar{
        margin-top: 50px;
        width: 100%;
        input {
                width: 100%;
                padding: 10px 10px 10px 30px;
                border: none;
                border-radius: 5px;
                background: #fff;
                box-shadow: 0px 0px 20px rgb(0, 0, 0,  0.25);
                border: 2px solid #fff;
                transition: all 0.3s;
                &:focus {
                    border: 2px solid red;
                    outline: none;
                }
        }
        &__list{
                position: relative;
                z-index: 10;
        }
        &__result{
                position: absolute;
                top: 41px;
                width: 40%;
                max-height: 300px;
                height: auto;
                overflow-y: scroll;
                background: #fff;
                border: 2px solid red;
                border-top: none;
                z-index: 10;
                display: flex;
                flex-direction: column;
                opacity: 0.95;
                transition: max-height 0.5s;
        }
        &__wrapper{
                display: flex;
                justify-content: start;
                margin-top: 4px;
                padding-bottom: 4px;
                align-items: center;
                border-bottom: 1px solid rgb(207, 203, 203);
        }
        &__img{
                width: 50px;
                height: 50px;
        }
        &__name{
                margin-left: 10px;                
        }

}