.error {
        color: #e53e3e;
        margin-top: 8px;
}
.searchChar{
        padding: 25px;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
        background-color: #fff;
        &__inputBtn{
                margin-top: 20px;
                display: flex;
                justify-content: space-between;
        }
        &__input{                
                width: 65%;
                display: flex;
                flex-direction: column;
        }
        &__link{
                color: blue;
                transition: all 0.3s;
                &:hover{
                        transform: scale(1.1);
                        color: blue;
                }
        }
        &__title{
                display: flex;
                justify-content: space-between;
                margin-top: 10px;
                &_done{
                        font-size: 20px;
                        color:green;
                        margin-top: 10px;
                }
                &_error{
                        font-size: 20px;
                        color:#9F0013;
                        margin-top: 10px;
                }
                .button{
                        .inner{
                                background-color: grey;
                                &:before{
                                        border-color: grey;
                              
                                }
                                &:after{
                                        border-color: grey;              
                                }
                        }                        
                }
                .button.button__main{
                        &:before{
                                background-color: grey
                        }
                        &:after{
                                background-color: grey;
                        }
    
                }                
        }
}
#name{       
        height: 40px;
        border: none;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
        padding: 5px;
        font-size: 15px;
}

